import React, { Fragment } from "react";
import Fade from "react-reveal/Fade";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFileDownload } from "@fortawesome/free-solid-svg-icons";
import resume from "../../assets/resume/Aniket's_Resume.pdf"

export default function Discover() {
  return (
    <Fragment>
      <Fade cascade>
        <div style={{ marginBottom: "150px" }} id="resume">
          <div style={{  margin: "0 auto", textAlign: "center" }}>
            <h2 style={{ marginBottom: "50px" }}>Interested? Pull my resume!</h2>
            <button className="styleme">
              <a href={resume} style={{ color: "inherit", height:"30px", width:"100%"}} download>
              <FontAwesomeIcon style={{textAlign:"center"}} icon={faFileDownload} />
              </a>
            </button>
          </div>
        </div>
      </Fade>
    </Fragment>
  );
}
