import React, { Fragment } from "react";
import { Row, Col } from "react-bootstrap";
import Fade from "react-reveal/Fade";
export default function Easein() {
  return (
    <Fragment>
      <h1 id="org" style={{ marginTop: "150px", textAlign:"center",textDecoration: "underline"}}>Organisations</h1>
      <Row
        style={{ marginTop: "50px", marginBottom: "100px" }}
        className="show-grid justify-center"
      >
        <Col
          lg={4}
          md={4}
          sm={12}
          style={{ paddingBottom: "2%" }}
          className="hover-me-1"
        >
          <h2
            style={{ margin: "0 auto", textAlign: "center" }}
            className="dissapear1"
          >
            NVCTI
          </h2>
          <Fade>
            <p
              style={{ fontSize: "18px", textAlign: "center" }}
              className="changeme"
            >
              I'm currently involved in website team of a Student Innovation Cell(SIC) working for Naresh Vashisht Centre Of Tinkering And Innovation (NVCTI),
              an Organisation to endure start ups among youths.
            </p>
          </Fade>
        </Col>

        <Col
          lg={4}
          md={4}
          sm={12}
          style={{ paddingBottom: "2%" }}
          className="hover-me-2"
        >
          <h2
            style={{ margin: "0 auto", textAlign: "center" }}
            className="dissapear2"
          >
            CIIE
          </h2>
          <Fade>
            <p style={{ fontSize: "18.6px", textAlign: "center" }}>
             I'm currently involved in website team for Centre For Innovation Incubation Entrepreneurship (CIIE), an organisation to push youths towards Entrepreneurship, it's affiliated to IIT Dhanbad.
            </p>
          </Fade>
        </Col>

        <Col
          lg={4}
          md={4}
          sm={12}
          style={{ paddingBottom: "2%" }}
          className="hover-me-3"
        >
          <h2
            style={{ margin: "0 auto", textAlign: "center" }}
            className="dissapear3"
          >
            BeingArtifex
          </h2>
          <Fade>
            <p style={{ fontSize: "18.6px", textAlign: "center" }}>
              A local startup by a group of our friends, I'm involved there as website developer. Idea is to provide free softskill development to younger ones 
              You can make a visit there <a href="http://beingartifex.in/">beingartifex.in</a>
            </p>
          </Fade>
        </Col>
      </Row>
    </Fragment>
  );
}
