import React from 'react';
import { Nav, Navbar } from 'react-bootstrap';
import Fade from 'react-reveal/Fade'; 

const NavbarComponent = ({ variant, logo }) => {
  const changeBackground = () => {
    let header = document.getElementsByClassName("header-nav")[0];
    if (window.scrollY < 100) {
      header.classList.remove("sticky");
    } else {
      header.classList.add("sticky");
    }
  };

  window.addEventListener("scroll", changeBackground);
  return (
    <>
      <Navbar
        className={`header-nav ${variant}`}
        fixed="top"
        collapseOnSelect
        expand="lg"
      >
        <Fade left cascade>
          <Navbar.Brand href="#home" className="brand-name ml-5" style={{color: 'white'}}>
            Checkout Page
          </Navbar.Brand>
        </Fade>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ml-auto mr-5">
            <Fade right cascade>
              <Nav.Link className="px-4" href="#details">
                Tools
              </Nav.Link>
              <Nav.Link className="px-4" href="#slideshow">
                Handy Frontends
              </Nav.Link>
              <Nav.Link className="px-4" href="#org">
                Organisations
              </Nav.Link>
              <Nav.Link className="px-4" href="#flip">
                More Skills
              </Nav.Link>
              <Nav.Link className="px-4" href="#resume">
                Resume
              </Nav.Link>
              <Nav.Link className="px-4" href="#projects">
                Projects
              </Nav.Link>
              <Nav.Link className="px-4" href="#about">
               About Me
              </Nav.Link>
            </Fade>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </>
  );
};
export default NavbarComponent;