import React, { Fragment } from "react";
import { Row, Col } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlus,
  faCode,
  faTrophy,
} from "@fortawesome/free-solid-svg-icons";
import Fade from "react-reveal/Fade";
export default function Flip() {
  return (
    <Fragment>
      <Row
        style={{ marginTop: "150px", marginBottom: "150px" }}
        className="show-grid justify-center"
        id="flip"
      >
        
          
        <Col
          lg={4}
          md={4}
          sm={12}
          style={{ paddingBottom: "2%", width: "100%" }}
          className="justify-center"
        >
          <Fade>
            <div className="flip-card">
              <div className="flip-card-inner">
                <div className="flip-card-front">
                  <FontAwesomeIcon icon={faPlus} size="2x" />
                  <h1> Other Fields</h1>
                </div>
                <div className="flip-card-back">
                  <h1 style={{ marginTop: "40%" }}>Other Fields</h1>
                  <p> Machine Learning , OpenCv3, good hand in Robot Operating System(ROS), Gazebo.</p>
                </div>
              </div>
            </div>
          </Fade>
        </Col>

        <Col
          lg={4}
          md={4}
          sm={12}
          style={{ paddingBottom: "2%", width: "100%" }}
          className="justify-center"
        >
          <Fade>
            <div className="flip-card">
              <div className="flip-card-inner">
                <div className="flip-card-front">
                  <FontAwesomeIcon icon={faCode} size="2x" />
                  <h1>programming Languages</h1>
                </div>
                <div className="flip-card-back">
                  <h1 style={{ marginTop: "40%" }}>Languages Known</h1>
                  <p>
                   C, C++, JavaScript, upto a good extent on Python.
                  </p>
                </div>
              </div>
            </div>
          </Fade>
        </Col>

        <Col
          lg={4}
          md={4}
          sm={12}
          style={{ paddingBottom: "2%", width: "100%" }}
          className="justify-center"
          
        >
          <Fade>
            <div className="flip-card" style={{borderRadius:"10%"}}>
              <div className="flip-card-inner">
                <div className="flip-card-front">
                  <FontAwesomeIcon icon={faTrophy} size="2x" />
                  <h1>Achievements</h1>
                </div>
                <div className="flip-card-back">
                  <h1 style={{ marginTop: "20%" }}>yeah!</h1>
                  <p>
                   <ul>
                     <li>
                       * 99.69 percentile in JEE Mains 2019
                     </li>
                     <li>
                       * AIR 8K in JEE Advanced 2019
                     </li>
                     <li>
                       * In top 5 teams of FallFest ,  a hackathon by ACM , IIT Dhanbad
                     </li>
                     <li>
                      * Stage2 qualifier of on going robotics competition Eyantra 2020
                     </li>

                   </ul>
                  </p>
                </div>
              </div>
            </div>
          </Fade>
        </Col>
      </Row>
    </Fragment>
  );
}
