import React from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFacebook, faGithub, faInstagram, faLinkedinIn, faWhatsapp } from '@fortawesome/free-brands-svg-icons';
import { Fade } from 'react-reveal';

const Footer = ({extraStyle}) => {
    return (
      <div className="word-section footer justify-center" style={extraStyle} id="about">
        <Fade top cascade>
          <div className="left">
            <h2>About me</h2>
            <p className="lead">
             I am a student of IIT (ISM) Dhanbad ,with a <b>strong addiction of finalising my work </b>, tech enthusiastic,
             and a rigid pillar of my team. I love to spent my spare time with sports and music. Contact me for further querries. <FontAwesomeIcon icon={faWhatsapp} />{" "}
            </p>
          </div>
        </Fade>
        <Fade top cascade>
        <div className="right" style={{justifyContent:"normal"}}>
          <h3 className="connect-text">Connect:</h3>
          <div className="social-links" style={{marginBottom:"20px"}}>
            {data.map((item, ind) => {
              const { icon, link, name } = item;
              return (
                <span key={item + ind}>
                  <a
                    href={link}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ paddingLeft: "0" }}
                  >
                    <FontAwesomeIcon icon={icon} className={name} />
                  </a>
                </span>
              );
            })}
          </div>
        </div>
        </Fade>
        <Fade bottom cascade>
        <div className="bottom-div jusitfy-align-center">
          <p className="first-text my-1">Made By Aniket Raj</p>
          <p className="second-text">
            <em>My Portfolio</em>
          </p>
        </div>
        </Fade>
      </div>
    );
}

export default Footer;

const data = [
  
  {
    icon: faInstagram,
    name: "instagram",
    link: "https://www.instagram.com/i_am_infinity_1_0/",
  },
  {
    icon: faLinkedinIn,
    name: "linkedin",
    link: "www.linkedin.com/in/aniket-raj-194711202",
  },
  {
    icon: faGithub,
    name: "Github",
    link: "https://github.com/aniketRaj01",
  }
];
