import React, { Fragment } from "react";
import { Row, Col } from "react-bootstrap";
import Fade from "react-reveal/Fade";
import src from '../../video/color.mp4'
import src2 from '../../video/flap.mp4';
import src3 from '../../video/news.mp4';
import src4 from '../../video/pp.mp4';
import src5 from '../../video/taptap.mp4';
import src6 from '../../video/tictac.mp4'

export default function Final() {
    return (
        <Fragment>
    <Fade top cascade>
        <Row >
        <Col
             lg={4}
             md={12}
             sm={12}
             style={{ marginBottom:"50px", borderRadius:"10%"}}
             
             >
               <a href="https://aniketraj01.github.io/dailynews/" style={{color:"inherit"}}>
   
           <video style={{boxShadow:"5px 10px #888888", borderRadius:"10%"}} height="100%" width="100%" autoPlay loop muted>
     <source src={src3} type="video/mp4"></source>
     
   </video>
   
   <div class="container">
       <h4><b>News App</b></h4>
       <p>API Calling From Times Of India</p>
     </div>
   
   
     </a>
             
             </Col>
             <Col
             lg={4}
             md={12}
             sm={12}
             style={{ marginBottom:"50px"}}
             >
        
        <a href="https://aniketraj01.github.io/TapTap/" style={{color:"inherit"}}>
   
           <video style={{boxShadow:"5px 10px #888888", borderRadius:"10%"}} height="100%" width="100%" autoPlay loop muted>
     <source src={src5} type="video/mp4"></source>
     
   </video>
   <div class="container">
       <h4><b>Tap And Play </b></h4>
       <p>Relaxing paperjs animation with javascript</p>
     </div>
   
   
     </a>
             </Col>
   
   
   
             <Col
             lg={4}
             md={12}
             sm={12}
             style={ {marginBottom:"50px"}}
             >
             <a href="https://aniketraj01.github.io/pingpong/" style={{color:"inherit"}}>
   
           <video style={{boxShadow:"5px 10px #888888", borderRadius:"10%"}} height="100%" width="100%" autoPlay loop muted>
     <source src={src6} type="video/mp4"></source>
     
   </video>
   
   <div class="container">
       <h4><b>Tic Tac Toe</b></h4>
       <p>Player Vs Player game</p>
     </div>
     </a>
             </Col>
        </Row>
        </Fade>
        <h1 style={{ marginTop: "150px", textAlign:"center",marginBottom:"200px"}} id="projects">And More on my github Profile....</h1>
       </Fragment>
    )
}
